<template>
  <div class="home">
    <h2>唯我音乐</h2>
    <keep-alive>
      <ul class="nav">
        <li>
          <router-link to="/recommend">推荐音乐</router-link>
        </li>
        <li>
          <router-link to="/hot">热歌榜</router-link>
        </li>
        <li>
          <router-link to="/search">搜索</router-link>
        </li>
      </ul>
    </keep-alive>
    <transition :name="transitionName">
      <keep-alive
        >
        <router-view
          @translateWhenSongId="translateWhenSongId($event)"
          @translatePlaylist="translatePlaylist($event)"
          :currentIndex='currentIndex'
        />
      </keep-alive>
    </transition>
  </div>
</template>

<script>
export default {
  name: "home",
  props:['currentIndex'],
  data() {
    return {
      transitionName: ""
    };
  },
  watch: {
    $route(to, from) {
      const toDepth = to.path.split("/").length;
      const fromDepth = from.path.split("/").length;
      this.transitionName = toDepth < fromDepth ? "slide-right" : "slide-left";
    }
  },
  methods: {
    translateWhenSongId(id) {
      this.$emit("translateWhenSongId", id);
    },
    translatePlaylist(playlist) {
      this.$emit("translatePlaylist", playlist);
    }
  }
};
</script>
<style lang="less">
.slide-right-enter-active,
.slide-right-leave-active,
.slide-left-enter-active,
.slide-left-leave-active {
  will-change: transform;
  transition: all 200ms;
  position: absolute;
}

.slide-right-enter {
  opacity: 1;
  // transform: translateX(-100%);
  transform: translate3d(-100%, 0, 0);
}
.slide-right-leave-active {
  opacity: 0;
  // transform: translateX(100%);
  transform: translate3d(100%, 0, 0);
}
.slide-left-enter {
  opacity: 1;
  // transform: translateX(100%);
  transform: translate3d(100%, 0, 0);
}
.slide-left-leave-active {
  opacity: 0;
  // transform: translateX(-100%);
  transform: translate3d(-100%, 0, 0);
}
.home {
  height: 100vh;
  overflow: hidden;
  h2 {
    text-align: center;
    font-size: 30px;
    font-family: "Pacifico", cursive;
    font-weight: bold;
    background: rgb(241, 68, 68);
    padding: 20px 0;
    color: white;
  }
  ul.nav {
    display: flex;
    border-bottom: 1px solid #e6e6e6;
    li {
      flex: 1;
      text-align: center;
      height: 41px;
      a {
        line-height: 40px;
        display: inline-block;
        &:active {
          background: none;
        }
        &.router-link-exact-active {
          color: red;
          border-bottom: 2px solid red;
        }
      }
    }
  }
}
</style>
