<template>
  <div id="app">
    <div id="nav"></div>
    <keep-alive>
      <transition :name="transitionName">
        <router-view
          @translateWhenSongId="translateWhenSongId($event)"
          :WhenMusiClickId="WhenMusiClickId"
          :isnowPlay="isnowPlay"
          @isnowPlayMusic="isnowPlayMusic($event)"
          @smallPlays="smallPlays($event)"
          :SongPlayTimeArr="SongPlayTimeArr"
          @PrevSongTure="PrevSongTure"
          @NextSongTure="NextSongTure"
          @translatePlaylist="translatePlaylist($event)"
          :currentIndex="currentIndex"
          :playlist="playlist"
          @translateWhenPlayTime="translateWhenPlayTime($event)"
          @tranlateplaymoder="tranlateplaymoder($event)"
          :lyrictext="lyrictext"
        />
      </transition>
    </keep-alive>
    <keep-alive>
      <FloatPalyNav
        :WhenMusiClickId="WhenMusiClickId"
        :smallPlay="smallPlay"
        :bigPlay="bigPlay"
        @isnowPlaydetector="isnowPlaydetector($event)"
        :isnowPlay="isnowPlay"
        @smallPlays="smallPlays($event)"
        @BackPlayTime="BackPlayTime($event)"
        :isPrevSong="isPrevSong"
        :isNextSong="isNextSong"
        :playlist="playlist"
        @backindex="backindex"
        :whenPlaytime="whenPlaytime"
        :playmoder="playmoder"
        @transinger="transinger"
        @senSongObj="senSongObj($event)"
      ></FloatPalyNav>
    </keep-alive>
  </div>
</template>
<script>
import FloatPalyNav from "@/views/FloatPalyNav.vue";
export default {
  components: {
    FloatPalyNav
  },
  data() {
    return {
      WhenMusiClickId: null,
      playlist: null,
      smallPlay: true,
      bigPlay: false,
      //现在是否播放音乐
      isnowPlay: false,
      SongPlayTimeArr: [],
      isPrevSong: false,
      isNextSong: false,
      currentIndex: null,
      //播放
      whenPlaytime: null,
      playmoder: null,
      lyrictext: null,
      transitionName: ""
    };
  },
  watch: {
    $route(to, from) {
      const toDepth = to.path.split("/").length;
      const fromDepth = from.path.split("/").length;
      this.transitionName = toDepth < fromDepth ? "slide-right" : "slide-left";
    }
  },
  methods: {
    transinger(n) {
      this.lyrictext = n;
    },
    BackPlayTime(Timearr) {
      this.SongPlayTimeArr = Timearr;
    },
    translateWhenSongId(id) {
      this.WhenMusiClickId = id;
      this.isnowPlay = true;
    },
    isnowPlaydetector(n) {
      this.isnowPlay = n;
    },
    isnowPlayMusic(n) {
      this.isnowPlay = n;
    },
    smallPlays(n) {
      this.smallPlay = n;
    },
    PrevSongTure(n) {
      this.isPrevSong = n;
    },
    NextSongTure(n) {
      this.isNextSong = n;
    },
    translatePlaylist(playlist) {
      this.playlist = playlist;
      this.currentIndex = playlist[0];
    },
    backindex(n) {
      this.currentIndex = n;
    },
    translateWhenPlayTime(n) {
      this.whenPlaytime = n;
    },
    tranlateplaymoder(n) {
      this.playmoder = n;
    },
    senSongObj(n) {
      this.WhenMusiClickId = n;
    }
  }
};
</script>
<style lang="less">
body {
  height: 100vh;
  overflow: hidden;
}
.slide-right-enter-active,
.slide-right-leave-active,
.slide-left-enter-active,
.slide-left-leave-active {
  will-change: transform;
  transition: all 200ms;
}

.slide-right-enter {
  opacity: 0;
  //   transform: translateX(-100%);
  transform: translate3d(-100%, 0, 0);
}
.slide-right-leave-active {
  opacity: 0;
  //   transform: translateX(100%);
  transform: translate3d(100%, 0, 0);
}
.slide-left-enter {
  opacity: 0;
  //   transform: translateX(100%);
  transform: translate3d(100%, 0, 0);
}
.slide-left-leave-active {
  opacity: 0;
  //   transform: translateX(-100%);
  transform: translate3d(-100%, 0, 0);
}
a {
  text-decoration: none;
  color: #2c3e50;
}
</style>
